<template>
  <div class="box">
		<div class="small-box" v-for="(item, index) in datas" :key="index" @click="goInnerClass(item.name)">
			<span class="name">{{item.name}}</span>
			<span class="quantity">{{item.monthlyCount}}</span>
		</div>
	</div>
</template>

<script>

export default {
	name: "Category",
	//direction 方向
	props: ["direction"],
	data() {
		return {
			datas: ""
		}
	},
	methods: {
		goInnerClass(item) {
			this.$router.push({path: "/InnerClass", query: {name: item, direction: this.direction}})
		}
	},
	created() {
		this.axios.get("/api/cats/lv2/statistics").then(
			response => {
				if(this.direction === "male") {
				this.datas = response.data.male
				} else if(this.direction === "female") {
				this.datas = response.data.female
				} else if(this.direction === "picture") {
				this.datas = response.data.picture
				} else {
					this.datas = response.data.press
				}
			}
		) 
	}
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.box {

	width: 100%;

	.small-box {
		display: inline-block;
		padding: 10px;
		width: calc(33% - 20px);
		height: 38px;

		span {
			display: block;
			height: 50%;
			text-align: center;
			line-height: 19px;
		}

		span.name {
			font-size: 15px;
			font-weight: bold;
			color: #333;
		}

		span.quantity {
			font-size: 12px;
			color: #ccc;
		}
	}
}
</style>
